	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Go is a new open-source programming language with an emphasis on simplicity, comprehensibility, and efficiency. It has the speed and safety of a statically-typed language and the lightweight feel of a scripting language. It has a simple feature set that is easy to understand and powerful to use, including a novel type system and concurrent programming primitives. This talk will dive into some real-world Go programs and discuss the Go idiom used in each.\r\n\r\nFOSDEM (Free and Open Source Development European Meeting) is a European event centered around Free and Open Source software development. It is aimed at developers and all interested in the Free and Open Source news in the world. Its goals are to enable developers to meet and to promote the awareness and use of free and open source software.","meta":{"duration":"PT59M20S"},"presenter":{"name":"Andrew Gerrand"},"source":{"id":"2-pPAvqyluI","provider":"youtube"},"slug":"golang-practical-go-programming","tag":["golang","FOSDEM 2011"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/2-pPAvqyluI/default.jpg","height":90,"width":120}},"title":"golang, practical Go Programming"}},{"node":{"description":"They say you can't teach an old dog new tricks, but what about a gopher? \n\nIn this talk we'll investigate a bunch of odd corners of the language, libraries, and tools that even seasoned Go programmers might not know.","meta":{"duration":"PT53M11S"},"presenter":{"name":"Andrew Gerrand"},"source":{"id":"UECh7X07m6E","provider":"youtube"},"slug":"stupid-gopher-tricks","tag":["golang","Golang UK Conference 2015"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/UECh7X07m6E/default.jpg","height":90,"width":120}},"title":"Stupid Gopher Tricks"}}]}},"pathContext":{"speaker":"Andrew Gerrand"}}

